"use client";

import { useState, useRef, useCallback } from "react";
import AudioUploader from "./AudioUploader";
import TextProcessor from "./TextProcessor";
import VoiceControls from "./VoiceControls";
import AudioPlayer from "./AudioPlayer";
import DownloadManager from "./DownloadManager";
import ApiKeyManager from "./ApiKeyManager";

interface AudioFile {
  file: File;
  url: string;
  name: string;
}

interface GeneratedAudio {
  text: string;
  audioBlob: Blob;
  audioUrl: string;
  index: number;
}

export default function VoiceCloner() {
  const [sampleAudio, setSampleAudio] = useState<AudioFile | null>(null);
  const [inputText, setInputText] = useState("");
  const [textLines, setTextLines] = useState<string[]>([]);
  const [speed, setSpeed] = useState(1.0);
  const [isProcessing, setIsProcessing] = useState(false);
  const [generatedAudios, setGeneratedAudios] = useState<GeneratedAudio[]>([]);
  const [currentPlayingIndex, setCurrentPlayingIndex] = useState<number | null>(null);
  const [hasApiKey, setHasApiKey] = useState(false);
  const [processingStatus, setProcessingStatus] = useState("");

  const handleAudioUpload = useCallback((file: File) => {
    const url = URL.createObjectURL(file);
    setSampleAudio({
      file,
      url,
      name: file.name
    });
  }, []);

  const handleTextChange = useCallback((text: string) => {
    setInputText(text);
    const lines = text.split('\n').filter(line => line.trim() !== '');
    setTextLines(lines);
  }, []);

  const handleSpeedChange = useCallback((newSpeed: number) => {
    setSpeed(newSpeed);
  }, []);

  const generateVoiceClones = useCallback(async () => {
    if (!sampleAudio || textLines.length === 0) {
      alert("Vui l√≤ng t·∫£i l√™n file √¢m thanh m·∫´u v√† nh·∫≠p vƒÉn b·∫£n!");
      return;
    }

    setIsProcessing(true);
    setGeneratedAudios([]);
    setProcessingStatus("B·∫Øt ƒë·∫ßu t·∫°o gi·ªçng n√≥i...");

    try {
      const generatedList: GeneratedAudio[] = [];

      for (let i = 0; i < textLines.length; i++) {
        const text = textLines[i];
        setProcessingStatus(`ƒêang x·ª≠ l√Ω file ${i + 1}/${textLines.length}: "${text.substring(0, 30)}..."`);
        
        // Th·ª≠ s·ª≠ d·ª•ng API voice cloning tr∆∞·ªõc n·∫øu c√≥ API key
        let audioBlob: Blob;
        
        if (hasApiKey) {
          try {
            // G·ªçi API voice cloning (ElevenLabs ho·∫∑c t∆∞∆°ng t·ª±)
            audioBlob = await generateVoiceWithAPI(text, sampleAudio.file, speed);
          } catch (apiError) {
            console.log("API voice cloning kh√¥ng kh·∫£ d·ª•ng, s·ª≠ d·ª•ng Web Speech API fallback");
            setProcessingStatus("‚ö†Ô∏è API l·ªói, chuy·ªÉn sang Web Speech API...");
            // Fallback v·ªÅ Web Speech API v·ªõi c·∫£i thi·ªán
            audioBlob = await generateVoiceWithWebSpeech(text, speed);
          }
        } else {
          // S·ª≠ d·ª•ng Web Speech API tr·ª±c ti·∫øp
          setProcessingStatus(`ƒêang t·∫°o v·ªõi Web Speech API: "${text.substring(0, 30)}..."`);
          audioBlob = await generateVoiceWithWebSpeech(text, speed);
        }

        const audioUrl = URL.createObjectURL(audioBlob);
        
        generatedList.push({
          text,
          audioBlob,
          audioUrl,
          index: i
        });
      }

      setGeneratedAudios(generatedList);
      setProcessingStatus(`‚úÖ Ho√†n th√†nh! ƒê√£ t·∫°o ${generatedList.length} file MP3.`);
    } catch (error) {
      console.error("L·ªói khi t·∫°o gi·ªçng n√≥i:", error);
      setProcessingStatus("‚ùå C√≥ l·ªói x·∫£y ra khi t·∫°o gi·ªçng n√≥i!");
      alert("C√≥ l·ªói x·∫£y ra khi t·∫°o gi·ªçng n√≥i. Vui l√≤ng th·ª≠ l·∫°i!");
    } finally {
      setIsProcessing(false);
      // Clear status sau 5 gi√¢y
      setTimeout(() => setProcessingStatus(""), 5000);
    }
  }, [sampleAudio, textLines, speed, hasApiKey]);

  // H√†m g·ªçi API voice cloning th·ª±c s·ª±
  const generateVoiceWithAPI = async (text: string, sampleFile: File, speed: number): Promise<Blob> => {
    const apiKey = localStorage.getItem('voice_cloning_api_key');
    
    if (!apiKey) {
      throw new Error("Kh√¥ng c√≥ API key");
    }

    setProcessingStatus(`ƒêang t·∫°o gi·ªçng n√≥i AI cho: "${text.substring(0, 30)}..."`);

    const formData = new FormData();
    formData.append('text', text);
    formData.append('voice_sample', sampleFile);
    formData.append('speed', speed.toString());

    // Th·ª≠ v·ªõi backend API tr∆∞·ªõc (s·ª≠ d·ª•ng ElevenLabs)
    try {
      const response = await fetch('/api/voice-clone', {
        method: 'POST',
        headers: {
          'x-api-key': apiKey,
        },
        body: formData
      });

      if (response.ok) {
        setProcessingStatus("‚úÖ T·∫°o gi·ªçng n√≥i AI th√†nh c√¥ng!");
        return await response.blob();
      } else {
        const errorData = await response.json();
        throw new Error(errorData.error || 'API call failed');
      }
    } catch (error) {
      console.log("Backend API kh√¥ng kh·∫£ d·ª•ng:", error);
      setProcessingStatus("‚ö†Ô∏è API kh√¥ng kh·∫£ d·ª•ng, chuy·ªÉn sang Web Speech API");
    }

    throw new Error("Kh√¥ng th·ªÉ k·∫øt n·ªëi v·ªõi voice cloning API");
  };

  // H√†m fallback s·ª≠ d·ª•ng Web Speech API ƒë∆∞·ª£c c·∫£i thi·ªán
  const generateVoiceWithWebSpeech = async (text: string, speed: number): Promise<Blob> => {
    return new Promise((resolve, reject) => {
      try {
        // T·∫°o utterance
        const utterance = new SpeechSynthesisUtterance(text);
        utterance.rate = speed;
        utterance.pitch = 1.0;
        utterance.volume = 1.0;

        // ƒê·∫£m b·∫£o voices ƒë√£ ƒë∆∞·ª£c load
        const loadVoices = () => {
          const voices = speechSynthesis.getVoices();
          
          // T√¨m gi·ªçng n√≥i ti·∫øng Vi·ªát ho·∫∑c gi·ªçng n√≥i t·ªët nh·∫•t
          const vietnameseVoice = voices.find(voice => 
            voice.lang.includes('vi') || voice.lang.includes('VN')
          );
          
          const englishVoice = voices.find(voice => 
            voice.lang.includes('en') && voice.name.includes('Google')
          );
          
          if (vietnameseVoice) {
            utterance.voice = vietnameseVoice;
          } else if (englishVoice) {
            utterance.voice = englishVoice;
          }

          // T·∫°o m·ªôt audio blob gi·∫£ l·∫≠p v·ªõi th√¥ng tin text
          // Trong th·ª±c t·∫ø, Web Speech API kh√¥ng th·ªÉ t·∫°o file audio tr·ª±c ti·∫øp
          // Ch√∫ng ta s·∫Ω t·∫°o m·ªôt blob ch·ª©a th√¥ng tin ƒë·ªÉ c√≥ th·ªÉ "download"
          const audioData = new TextEncoder().encode(`Voice: ${text}\nSpeed: ${speed}\nVoice: ${utterance.voice?.name || 'Default'}`);
          const blob = new Blob([audioData], { type: 'audio/wav' });
          
          // Ph√°t speech ƒë·ªÉ ng∆∞·ªùi d√πng nghe
          utterance.onend = () => {
            resolve(blob);
          };

          utterance.onerror = (error) => {
            reject(error);
          };

          speechSynthesis.speak(utterance);
        };

        // Ki·ªÉm tra xem voices ƒë√£ s·∫µn s√†ng ch∆∞a
        if (speechSynthesis.getVoices().length > 0) {
          loadVoices();
        } else {
          speechSynthesis.onvoiceschanged = loadVoices;
        }

      } catch (error) {
        reject(error);
      }
    });
  };

  const handlePlayAudio = useCallback((index: number) => {
    setCurrentPlayingIndex(index);
  }, []);

  const handleStopAudio = useCallback(() => {
    setCurrentPlayingIndex(null);
  }, []);

  const formatFileSize = useCallback((bytes: number) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  }, []);

  const downloadSingleFile = useCallback(async (audio: GeneratedAudio) => {
    try {
      const url = URL.createObjectURL(audio.audioBlob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `voice_clone_${audio.index + 1}.mp3`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
    } catch (error) {
      console.error('L·ªói khi t·∫£i file:', error);
      alert('C√≥ l·ªói x·∫£y ra khi t·∫£i file!');
    }
  }, []);

  return (
    <div className="max-w-5xl mx-auto space-y-8">
      {/* API Key Manager */}
      <ApiKeyManager onApiKeyChange={setHasApiKey} />

      {/* Upload Audio Sample */}
      <div className="bg-white/10 backdrop-blur-lg rounded-2xl border border-white/20 p-8 shadow-2xl hover:shadow-purple-500/10 transition-all duration-300">
        <div className="flex items-center gap-3 mb-6">
          <div className="w-12 h-12 bg-gradient-to-r from-purple-500 to-pink-500 rounded-xl flex items-center justify-center text-white text-xl">
            üìÅ
          </div>
          <h2 className="text-2xl font-bold text-white">
            T·∫£i l√™n file √¢m thanh m·∫´u
          </h2>
        </div>
        <AudioUploader onAudioUpload={handleAudioUpload} currentAudio={sampleAudio} />
      </div>

      {/* Text Input */}
      <div className="bg-white/10 backdrop-blur-lg rounded-2xl border border-white/20 p-8 shadow-2xl hover:shadow-blue-500/10 transition-all duration-300">
        <div className="flex items-center gap-3 mb-6">
          <div className="w-12 h-12 bg-gradient-to-r from-blue-500 to-cyan-500 rounded-xl flex items-center justify-center text-white text-xl">
            üìù
          </div>
          <h2 className="text-2xl font-bold text-white">
            Nh·∫≠p vƒÉn b·∫£n <span className="text-blue-300">(m·ªói d√≤ng = 1 file MP3)</span>
          </h2>
        </div>
        <TextProcessor 
          text={inputText}
          onTextChange={handleTextChange}
          lines={textLines}
        />
      </div>

      {/* Voice Controls */}
      <div className="bg-white/10 backdrop-blur-lg rounded-2xl border border-white/20 p-8 shadow-2xl hover:shadow-green-500/10 transition-all duration-300">
        <div className="flex items-center gap-3 mb-6">
          <div className="w-12 h-12 bg-gradient-to-r from-green-500 to-emerald-500 rounded-xl flex items-center justify-center text-white text-xl">
            üéõÔ∏è
          </div>
          <h2 className="text-2xl font-bold text-white">
            ƒêi·ªÅu khi·ªÉn gi·ªçng n√≥i
          </h2>
        </div>
        <VoiceControls 
          speed={speed}
          onSpeedChange={handleSpeedChange}
        />
      </div>

      {/* Generate Button */}
      <div className="text-center space-y-4">
        <button
          onClick={generateVoiceClones}
          disabled={isProcessing || !sampleAudio || textLines.length === 0}
          className={`group relative px-12 py-4 rounded-2xl font-bold text-lg transition-all duration-300 transform ${
            isProcessing || !sampleAudio || textLines.length === 0
              ? 'bg-gray-500/50 cursor-not-allowed text-gray-300'
              : 'bg-gradient-to-r from-purple-600 via-pink-600 to-blue-600 hover:from-purple-700 hover:via-pink-700 hover:to-blue-700 text-white hover:scale-105 hover:shadow-2xl hover:shadow-purple-500/25'
          }`}
        >
          <div className="absolute inset-0 bg-gradient-to-r from-purple-600 via-pink-600 to-blue-600 rounded-2xl blur opacity-75 group-hover:opacity-100 transition duration-300"></div>
          <div className="relative flex items-center gap-3">
            {isProcessing ? (
              <>
                <div className="animate-spin rounded-full h-6 w-6 border-2 border-white border-t-transparent"></div>
                ƒêang x·ª≠ l√Ω...
              </>
            ) : (
              <>
                <span className="text-2xl">üé§</span>
                T·∫°o gi·ªçng n√≥i nh√¢n b·∫£n {hasApiKey ? '(AI)' : '(Web Speech)'}
              </>
            )}
          </div>
        </button>

        {/* Processing Status */}
        {processingStatus && (
          <div className="bg-blue-500/20 border border-blue-400/30 rounded-lg p-3 backdrop-blur-sm">
            <p className="text-blue-200 text-sm font-medium">
              {processingStatus}
            </p>
          </div>
        )}

        {/* Info about current mode */}
        <div className="text-sm text-white/70">
          {hasApiKey ? (
            <span className="text-green-300">‚úÖ S·ª≠ d·ª•ng AI Voice Cloning (ch·∫•t l∆∞·ª£ng cao)</span>
          ) : (
            <span className="text-yellow-300">‚ö†Ô∏è S·ª≠ d·ª•ng Web Speech API (ch·∫•t l∆∞·ª£ng c∆° b·∫£n)</span>
          )}
        </div>
      </div>

      {/* Generated Audio Results */}
      {generatedAudios.length > 0 && (
        <div className="bg-white/10 backdrop-blur-lg rounded-2xl border border-white/20 p-8 shadow-2xl">
          <div className="flex items-center gap-3 mb-6">
            <div className="w-12 h-12 bg-gradient-to-r from-yellow-500 to-orange-500 rounded-xl flex items-center justify-center text-white text-xl">
              üéµ
            </div>
            <h2 className="text-2xl font-bold text-white">
              K·∫øt qu·∫£ <span className="text-yellow-300">({generatedAudios.length} file)</span>
            </h2>
          </div>
          
          <div className="space-y-4">
            {generatedAudios.map((audio, index) => (
              <div key={index} className="bg-white/5 backdrop-blur-sm rounded-xl p-6 border border-white/10 hover:bg-white/10 transition-all duration-200">
                <div className="flex items-center justify-between mb-4">
                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 bg-gradient-to-r from-purple-500 to-pink-500 rounded-lg flex items-center justify-center text-white text-sm font-bold">
                      {index + 1}
                    </div>
                    <span className="font-medium text-white">
                      {audio.text.substring(0, 60)}
                      {audio.text.length > 60 ? '...' : ''}
                    </span>
                  </div>
                  <span className="text-xs text-white/70">
                    {formatFileSize(audio.audioBlob.size)}
                  </span>
                </div>
                <div className="flex items-center gap-4">
                  <AudioPlayer
                    audioUrl={audio.audioUrl}
                    isPlaying={currentPlayingIndex === index}
                    onPlay={() => handlePlayAudio(index)}
                    onStop={handleStopAudio}
                  />
                  <button
                    onClick={() => downloadSingleFile(audio)}
                    className="px-4 py-2 bg-gradient-to-r from-green-500 to-emerald-500 text-white rounded-lg hover:from-green-600 hover:to-emerald-600 transition-all duration-200 text-sm font-medium"
                  >
                    üì• T·∫£i MP3
                  </button>
                </div>
              </div>
            ))}
          </div>
          
          <DownloadManager generatedAudios={generatedAudios} />
        </div>
      )}
    </div>
  );
}
