"use client";

import { useRef, useCallback } from "react";

interface AudioFile {
  file: File;
  url: string;
  name: string;
}

interface AudioUploaderProps {
  onAudioUpload: (file: File) => void;
  currentAudio: AudioFile | null;
}

export default function AudioUploader({ onAudioUpload, currentAudio }: AudioUploaderProps) {
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFileSelect = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      // Ki·ªÉm tra ƒë·ªãnh d·∫°ng file
      const allowedTypes = ['audio/wav', 'audio/mp3', 'audio/mpeg', 'audio/ogg'];
      if (!allowedTypes.includes(file.type)) {
        alert('Vui l√≤ng ch·ªçn file √¢m thanh h·ª£p l·ªá (WAV, MP3, OGG)');
        return;
      }

      // Ki·ªÉm tra k√≠ch th∆∞·ªõc file (t·ªëi ƒëa 50MB)
      if (file.size > 50 * 1024 * 1024) {
        alert('File qu√° l·ªõn! Vui l√≤ng ch·ªçn file nh·ªè h∆°n 50MB');
        return;
      }

      onAudioUpload(file);
    }
  }, [onAudioUpload]);

  const handleDrop = useCallback((event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    const file = event.dataTransfer.files[0];
    if (file) {
      const allowedTypes = ['audio/wav', 'audio/mp3', 'audio/mpeg', 'audio/ogg'];
      if (!allowedTypes.includes(file.type)) {
        alert('Vui l√≤ng ch·ªçn file √¢m thanh h·ª£p l·ªá (WAV, MP3, OGG)');
        return;
      }

      if (file.size > 50 * 1024 * 1024) {
        alert('File qu√° l·ªõn! Vui l√≤ng ch·ªçn file nh·ªè h∆°n 50MB');
        return;
      }

      onAudioUpload(file);
    }
  }, [onAudioUpload]);

  const handleDragOver = useCallback((event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
  }, []);

  const handleClick = useCallback(() => {
    fileInputRef.current?.click();
  }, []);

  const formatFileSize = (bytes: number) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  return (
    <div className="space-y-4">
      <div
        onClick={handleClick}
        onDrop={handleDrop}
        onDragOver={handleDragOver}
        className="border-2 border-dashed border-purple-400/50 rounded-xl p-8 text-center cursor-pointer hover:border-purple-400 hover:bg-purple-500/10 transition-all duration-300 group"
      >
        <div className="space-y-4">
          <div className="text-6xl group-hover:scale-110 transition-transform duration-300">üéµ</div>
          <p className="text-xl font-semibold text-white group-hover:text-purple-300 transition-colors">
            K√©o th·∫£ file √¢m thanh v√†o ƒë√¢y ho·∫∑c click ƒë·ªÉ ch·ªçn
          </p>
          <p className="text-white/70">
            H·ªó tr·ª£: <span className="text-purple-300 font-medium">WAV, MP3, OGG</span> (t·ªëi ƒëa 50MB)
          </p>
        </div>
      </div>

      <input
        ref={fileInputRef}
        type="file"
        accept="audio/*"
        onChange={handleFileSelect}
        className="hidden"
      />

      {currentAudio && (
        <div className="bg-gradient-to-r from-green-500/20 to-emerald-500/20 backdrop-blur-sm border border-green-400/30 rounded-xl p-6 mt-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="w-12 h-12 bg-gradient-to-r from-green-500 to-emerald-500 rounded-xl flex items-center justify-center text-2xl">
                ‚úÖ
              </div>
              <div>
                <p className="font-semibold text-white text-lg">{currentAudio.name}</p>
                <p className="text-green-300 font-medium">
                  {formatFileSize(currentAudio.file.size)}
                </p>
              </div>
            </div>
            <div className="bg-white/10 backdrop-blur-sm rounded-lg p-2">
              <audio controls className="h-8">
                <source src={currentAudio.url} type={currentAudio.file.type} />
                Tr√¨nh duy·ªát kh√¥ng h·ªó tr·ª£ ph√°t √¢m thanh.
              </audio>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
